.aside-projects-nav__wrapper-left {
  text-align: left;
}

.aside-projects-nav__wrapper-center {
  text-align: center;
}

.aside-projects-nav__wrapper-right {
  text-align: right;
}

.aside-projects-nav__prev, .aside-projects-nav__next, .aside-projects-nav__all {
  display: inline-block;
  color: #8f8f8f;
}

.aside-projects-nav__prev:before {
  content: '\23';
  display: inline-block;
  font-family: 'ElegantIcons';
  speak: none;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 26px;
  vertical-align: middle;
  margin-right: 20px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.aside-projects-nav__prev:hover:before {
  transform: translateX(-5px);
}

.aside-projects-nav__next:after {
  content: '\24';
  display: inline-block;
  font-family: 'ElegantIcons';
  speak: none;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 26px;
  vertical-align: middle;
  margin-left: 20px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.aside-projects-nav__next:hover:after {
  transform: translateX(5px);
}

.aside-projects-nav__all:after {
  content: '\e08d';
  font-family: 'ElegantIcons';
  speak: none;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 32px;
  vertical-align: middle;
}

.aside-projects-nav__col {
  margin-top: 15px;
  margin-bottom: 15px;
}

