/* ======================================================================== */
/* 33. menu */
/* ======================================================================== */
.menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.menu > li {
  position: relative;
  display: inline-block;
  margin-right: 42px;
}

.menu > li > a {
  display: block;
  color: #333333;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 0.1px;
  line-height: 2;
}

.menu > li > a:hover {
  color: #333333;
}

.menu > li:last-child {
  margin-right: 0;
}

.menu > li.menu-item-has-children > a:after {
  content: '\f107';
  font-family: 'FontAwesome';
  font-size: 14px;
  display: inline-block;
  vertical-align: middle;
  margin-left: 8px;
}

.menu > li.menu-item-has-children > a:hover ~ ul {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

.menu > li:not(.menu-item-has-children) > a:before {
  content: '';
  position: absolute;
  width: 0;
  height: 1px;
  background-color: #333333;
  bottom: 0;
  right: 1px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.menu > li:not(.menu-item-has-children) > a:hover {
  color: #333333;
}

.menu > li:not(.menu-item-has-children) > a:hover:before {
  left: 1px;
  width: calc(100% - 1px);
}

.menu .sub-menu {
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  list-style-type: none;
  padding: 0;
  margin: 0;
  padding: 10px 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transform: translate(0, 10px);
  background-color: #ffffff;
  border-radius: 4px;
  box-shadow: 0px 3px 14px 0px rgba(162, 162, 162, 0.65);
  z-index: 50;
}

.menu .sub-menu > li {
  position: relative;
  display: block;
  white-space: nowrap;
}

.menu .sub-menu > li a {
  display: flex;
  align-items: center;
  padding: 10px 30px;
  color: #333333;
}

.menu .sub-menu > li a:hover {
  color: var(--color-accent-primary);
}

.menu .sub-menu > li.menu-item-has-children > a:after {
  content: '\f105';
  font-family: 'FontAwesome';
  font-size: 14px;
  display: inline-block;
  vertical-align: middle;
  margin-left: auto;
  margin-top: 2px;
  padding-left: 20px;
}

.menu .sub-menu > li.menu-item-has-children > a:hover ~ ul {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

.menu .sub-menu ul {
  top: -10px;
  left: calc(100% - 10px);
  transform: translate(10px, 0);
  width: 100%;
}

.menu .sub-menu:hover {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

/* ======================================================================== */
/* 34. menuOverlay */
/* ======================================================================== */
.menu-overlay {
  position: relative;
  margin-top: auto;
  margin-bottom: auto;
  list-style-type: none;
  text-align: center;
  z-index: 50;
}

.menu-overlay > li {
  display: block;
  margin: 20px 0;
}

.menu-overlay > li > a {
  position: relative;
  display: block;
  overflow: hidden;
  color: #ffffff;
  font-size: 40px;
  font-weight: bold;
}

.menu-overlay > li > a:hover {
  color: #adadad;
}

.menu-overlay > li:not(.menu-item-has-children) > a span {
  display: inline-block;
  position: relative;
}

.menu-overlay > li:not(.menu-item-has-children) > a span:before {
  content: '';
  position: absolute;
  width: 0;
  height: 1px;
  background-color: #ffffff;
  bottom: 0;
  right: 5px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.menu-overlay > li:not(.menu-item-has-children) > a:hover {
  color: #ffffff;
}

.menu-overlay > li:not(.menu-item-has-children) > a:hover span:before {
  left: 5px;
  width: calc(100% - 5px);
}

.menu-overlay .sub-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  list-style-type: none;
  padding: 0;
  margin: 0;
  z-index: 100;
}

.menu-overlay .sub-menu > li {
  display: block;
}

.menu-overlay .sub-menu > li > a {
  display: block;
  position: relative;
  font-size: 24px;
  font-weight: bold;
  overflow: hidden;
  color: #ffffff;
}

.menu-overlay .sub-menu > li > a span {
  display: inline-block;
}

.menu-overlay .sub-menu > li > a .menu-overlay__item-wrapper {
  padding: 10px 15px;
}

.menu-overlay .sub-menu > li > a:hover {
  color: #adadad;
}

.menu-overlay .sub-menu > li:not(.menu-item-has-children) > a:hover {
  color: #ffffff;
}

.menu-overlay .sub-menu > li:not(.menu-item-has-children) > a .menu-overlay__item-wrapper:hover:before {
  left: 15px;
  width: calc(100% - 30px);
}

.menu-overlay .sub-menu > li:not(.menu-item-has-children) > a .menu-overlay__item-wrapper:before {
  content: '';
  position: absolute;
  width: 0;
  height: 1px;
  background-color: #ffffff;
  bottom: 10px;
  right: 15px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

@media only screen and (max-width: 767px) {
  .menu-overlay > li > a {
    font-size: 30px;
  }
  .menu-overlay .sub-menu > li > a {
    font-size: 18px;
  }
}

