/* 64. slider */
/* ======================================================================== */
.slider-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: -3px;
}

.slider-nav__dot {
  position: relative;
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.slider-nav__dot:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 7px;
  height: 7px;
  border-style: solid;
  border-width: 3.5px;
  border-color: #ffffff;
  border-radius: 50%;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.slider-nav__dot:not(.slider-nav__dot_active):hover:after {
  opacity: .5;
}

.slider-nav__dot_active:after {
  transform: translate(-50%, -50%) scale(1.5714);
  border-width: 1px;
}

.slider__prev, .slider__next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 52px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  cursor: pointer;
  color: #ffffff;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.slider__prev:after, .slider__next:after {
  font-family: 'ElegantIcons';
  speak: none;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 23px;
}

.slider__prev:hover, .slider__next:hover {
  background-color: var(--color-accent-primary);
  border-color: var(--color-accent-primary);
  color: #ffffff;
}

.slider__prev:focus, .slider__next:focus {
  outline: none;
}

.slider__prev.swiper-button-disabled, .slider__next.swiper-button-disabled {
  opacity: 0;
  visibility: hidden;
}

.slider__prev:after {
  content: '\23';
}

.slider__next:after {
  content: '\24';
}

@media only screen and (max-width: 1200px) {
  .slider__prev, .slider__next {
    width: 40px;
    height: 40px;
  }
  .slider__prev:after, .slider__next:after {
    font-size: 16px;
  }
}

@media only screen and (max-width: 767px) {
