/* ======================================================================== */
/* 73. social */
/* ======================================================================== */
.social {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.social__item {
  display: inline-block;
  margin: 0 6px;
}

.social__icon {
  display: flex !important;
  align-items: center;
  justify-content: center;
  width: 50px !important;
  height: 50px !important;
  font-size: 18px;
  color: #ffffff;
  background-color: #8f8f8f;
  border-radius: 100%;
}

.social__icon:hover {
  color: #ffffff;
  background-color: var(--color-accent-primary);
}

@media only screen and (max-width: 1200px) {
  .social__icon {
    width: 40px !important;
    height: 40px !important;
    font-size: 14px;
  }
}

/* ======================================================================== */
/* 74. tabs */
/* ======================================================================== */
.tabs {
  text-align: center;
  position: relative;
  font-family: "Montserrat", sans-serif;
  padding-bottom: 15px;
}

.tabs__header {
  margin-bottom: 60px;
}

.tabs__header h2 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 18px;
  line-height: 1.5;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 1px;
}

.tabs__item {
  cursor: pointer;
  color: #8f8f8f;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.tabs__item h4 {
  margin-top: 0;
  margin-bottom: 0;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
}

.tabs__item:hover {
  color: inherit;
}

.tabs__item_active {
  color: inherit;
}

.tabs__item_active .tabs__icon {
  color: var(--color-accent-primary);
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.tabs__icon {
  font-size: 50px;
  margin-bottom: 5px;
}

.tabs__underline {
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  background-color: #ffffff;
  width: 0;
  height: 1px;
}

@media only screen and (max-width: 991px) {
  .tabs__underline {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .tabs__item {
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .tabs__header {
    margin-bottom: 30px;
  }
}

/* ======================================================================== */
/* 75. tags */
/* ======================================================================== */
/* ======================================================================== */
/* 76. typography */
/* ======================================================================== */
h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 60px;
  margin: 0.25em 0;
  line-height: 1.2;
}

h2 {
  font-size: 50px;
  margin: 0.5em 0;
  line-height: 1.25;
}

h3 {
  font-size: 46px;
  margin: 0.75em 0;
  line-height: 1.3;
}

h4 {
  font-size: 30px;
  margin: 1em 0;
  line-height: 1.4;
}

h5 {
  font-size: 21px;
  margin: 1em 0;
}

h6 {
  font-size: 18px;
  margin: 1em 0;
}

p {
  margin-top: 1em;
  margin-bottom: 1em;
}

blockquote {
  display: block;
  font-size: 20px;
  line-height: 1.5;
  margin-top: 2em;
  margin-bottom: 2em;
}

blockquote p {
  margin-top: 0.5em;
}

blockquote cite {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: bold;
  font-style: normal;
  margin-top: 20px;
}

blockquote:before {
  content: '{';
  color: var(--color-accent-primary);
  font-size: 40px;
  margin-left: -10px;
}

@media only screen and (max-width: 1200px) {
  h1 {
    font-size: 46px;
  }
  h2 {
    font-size: 37px;
  }
  h3 {
    font-size: 34px;
  }
  h4 {
    font-size: 25px;
  }
  h5 {
    font-size: 18px;
  }
  h6 {
    font-size: 16px;
  }
}

@media only screen and (max-width: 767px) {
  h1 {
    font-size: 42px;
  }
  h2 {
    font-size: 33px;
  }
  h3 {
    font-size: 30px;
  }
  h4 {
    font-size: 22px;
  }
  h5 {
    font-size: 16px;
  }
  h6 {
    font-size: 14px;
  }
}

/* ======================================================================== */
/* 77. utilities */
/* ======================================================================== */
.text-left {
  text-align: left !important;
}

.text-center {
  text-align: center !important;
}

.text-right {
  text-align: right !important;
}

.row-80 {
  margin-left: -40px;
  margin-right: -40px;
}

.overflow {
  position: relative;
  overflow: hidden;
}

.overflow__curtain {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
}

.overflow__content {
  width: 100%;
  height: 100%;
}

.position-relative {
  position: relative;
}

.color-white {
  color: #ffffff !important;
}

.color-accent {
  color: var(--color-accent-primary) !important;
}

.color-black {
  color: #1a1a1a !important;
}

.color-dark {
  color: #333333 !important;
}

.blurred {
  filter: blur(7px);
}

.of-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.of-contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
  font-family: 'object-fit: contain;';
}

.split-line {
  overflow: hidden;
}

.elegant-icons, .figure-pricing__feature_yes:before, .figure-pricing__feature_no:before, blockquote:before {
  font-family: 'ElegantIcons';
  speak: none;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

