/* ======================================================================== */
/* 50. sectionIntro */
/* ======================================================================== */
.section-intro
  overflow: hidden
  h1
    font-family: $font-secondary
    font-size: 60px
    line-height: 1.2
    font-weight: bold
    letter-spacing: 0.5px
    margin-top: 0
    margin-bottom: 0.5em
  p
    font-size: 20px
.section-intro__wrapper-content
  padding-left: 215px
  padding-top: 160px
  padding-bottom: 160px
.section-intro__wrapper-backgrounds
  position: absolute
  top: 0
  left: 0
  width: 100%
  height: 100%
  z-index: -1
.section-intro__wrapper-sd
  position: absolute
  display: inline-block
  bottom: 18px
  left: 38%
  z-index: 60

.section-intro__diagonal-curtain-1, .section-intro__diagonal-curtain-2
  position: absolute
  width: 3000px
  height: 3000px
.section-intro__diagonal-curtain-1
  top: 100%
  // right: 200px
  right: 20%
  transform: rotate(45deg)
  transform-origin: top right
  background-color: $accent
.section-intro__diagonal-curtain-2
  bottom: 100%
  // right: 200px
  right: 20%
  transform: rotate(-45deg)
  transform-origin: bottom right
  background-color: $accent
.section-intro__diagonal-line-1, .section-intro__diagonal-line-2
  position: absolute
  right: calc(20% - 32px)
  height: 10px
  width: 3000px
  background-color: $accent
.section-intro__diagonal-line-1
  bottom: calc(100% + 10px)
  transform: rotate(-45deg)
  transform-origin: top right
.section-intro__diagonal-line-2
  top: calc(100% + 10px)
  transform: rotate(45deg)
  transform-origin: bottom right


