@media only screen and (max-width: $xxl)
  .section-intro
    h1
      font-size: 46px
    p
      font-size: 18px
  .section-intro__wrapper-content
    padding-left: 50px

@media only screen and (max-width: $md)
  .section-intro__wrapper-content
    padding-left: 15px
    padding-right: 15px
    text-align: center
  .section-intro__wrapper-sd
    left: 50%
    transform: translateX(-50%)
@media only screen and (max-width: $sm)
  .section-intro
    h1
      font-size: 30px
    p
      font-size: 16px
