@function rem($size) {
  $remSize: $size / $em-base;
  @return #{$remSize}rem; }
@mixin trans1 {
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.150, 0.860); }
@mixin trans2 {
  transition: all 0.6s cubic-bezier(0.785, 0.135, 0.150, 0.860); }
@mixin reset-ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

@mixin link-underline {
  &:after, &:before {
    content: '';
    position: absolute;
    bottom: 0;
    width: 0;
    right: 0;
    height: 1px;
    background-color: $white; }
  &:before {
    transition: width .4s cubic-bezier(0.51, 0.18, 0, 0.88) .1s; }
  &:after {
    transition: width .2s cubic-bezier(0.29, 0.18, 0.26, 0.83); }
  &:after, &:before {
    width: calc(100% - 10px);
    left: 0; } }

@mixin material-icons {
  font-family: 'Material Icons';
  font-style: normal;
  letter-spacing: normal;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  font-feature-settings: 'liga';
  ms-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility; }

@mixin shadow {
  box-shadow: 0px 0px 30px 0px rgba(24, 24, 24, 0.1); }

$em-base: 15px;
$font-primary: 'Open Sans', sans-serif;
$font-secondary: 'Montserrat', sans-serif;

// Color palette

$black: #1a1a1a;
$black2: #0f0f0f;
$dark: #333333;
$accent: var(--color-accent-primary);
$accent2: var(--color-accent-secondary);
$light: #f5f5f5;
$light2: #ebebeb;
$white: #ffffff;
$gray: #adadad;
$gray2: #8f8f8f;

// Responsive breakpoints
$xxl: 1400px;
$xl: 1200px;
$lg: 1024px;
$md: 991px;
$sm: 767px;
$xs: 576px;

 :root {
	--color-accent-primary: #1869ff;
	--color-accent-primary-rgb: 24, 105, 255;
	--color-accent-secondary: #b388ff;
	--color-accent-secondary-rgb: 179, 136, 255;
	--font-primary: 'Open Sans';
	--font-secondary: 'Montserrat'; }
html {
	font-size: $em-base;
	-webkit-font-smoothing: antialiased;
	text-rendering: optimizeLegibility; }
body {
	position: relative;
	width: 100%;
	min-width: 320px;
	// color: $dark
	font-family: $font-primary;
	font-size: 100%;
	line-height: 1.53; }
.body_lock-scroll {
	overflow: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	height: 100%; }
img {
	max-width: 100%;
	height: auto; }
a {
	@include trans1;
	&:hover, &:focus {
		text-decoration: none; }
	&:focus {
		outline: none; } }
.page-wrapper {
	@include trans1; }
.page-wrapper_hidden {
	opacity: 0;
	visibility: hidden; }
.jarallax, .jarallax-video {
	overflow: hidden; }

/* ======================================================================== */
/* 1. articlePost */
/* ======================================================================== */
.post {}
.post__content, .post__comments {
  > *:not( .alignwide ):not( .alignfull ) {
    margin-left: auto;
    margin-right: auto;
    max-width: 800px;
    padding-left: 15px;
    padding-right: 15px; }
  > *.alignwide {
    max-width: 1170px;
    margin-left: auto;
    margin-right: auto;
    display: block; } }
.post__content, .post__comments {
  img:not(.avatar) {
    margin-top: 3em;
    margin-bottom: 3em; } }
.post__header {}
.post__header {
  max-width: 800px;
  margin-bottom: 1.5em;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  h2 {
    font-family: $font-secondary;
    font-size: 46px;
    font-weight: bold; } }
.post__footer {
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  border-top: 1px solid #e5e5e5;
  margin-top: 5em; }
.post__properties {
  max-width: 770px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  .properties {
    display: flex;
    align-items: center;
    justify-content: space-between; } }
.post__comments {
  margin-top: 120px; }

@media only screen and (max-width: $xxl) {
  .post__comments {
    margin-top: 80px; } }

@media only screen and (max-width: $xl) {
  .post__content > *.alignwide, .post__comments > *.alignwide {
    max-width: 100%; }
  .post__content > *:not(.alignwide):not(.alignfull), .post__comments > *:not(.alignwide):not(.alignfull) {
    max-width: 800px; } }

@media only screen and (max-width: $sm) {
  .post__header h2 {
    font-size: 33px; }
  .post__comments {
    margin-top: 60px; } }

