/* ======================================================================== */
/* 8. asideProjectsNav */
/* ======================================================================== */
.aside-projects-nav {
  padding: 70px 0; }
.aside-projects-nav__wrapper-left {
  text-align: left; }
.aside-projects-nav__wrapper-center {
  text-align: center; }
.aside-projects-nav__wrapper-right {
  text-align: right; }
.aside-projects-nav__prev, .aside-projects-nav__next, .aside-projects-nav__all {
  display: inline-block;
  color: $gray2; }
.aside-projects-nav__prev {
  &:before {
    content: '\23';
    display: inline-block;
    font-family: 'ElegantIcons';
    speak: none;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 26px;
    vertical-align: middle;
    margin-right: 20px;
    @include trans1; }
  &:hover {
    &:before {
      transform: translateX(-5px); } } }
.aside-projects-nav__next {
  &:after {
    content: '\24';
    display: inline-block;
    font-family: 'ElegantIcons';
    speak: none;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 26px;
    vertical-align: middle;
    margin-left: 20px;
    @include trans1; }
  &:hover {
    &:after {
      transform: translateX(5px); } } }
.aside-projects-nav__all {
  &:after {
    content: '\e08d';
    font-family: 'ElegantIcons';
    speak: none;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 32px;
    vertical-align: middle; } }
.aside-projects-nav__col {
  margin-top: 15px;
  margin-bottom: 15px; }

@media only screen and (max-width: $xxl) {
  .aside-projects-nav {
    padding: 50px 0; } }

@media only screen and (max-width: $xl) {
  .aside-projects-nav {
    padding: 30px 0; } }

@media only screen and (max-width: $sm) {
  .aside-projects-nav__wrapper-left, .aside-projects-nav__wrapper-center, .aside-projects-nav__wrapper-right {
    text-align: center; } }

/* ======================================================================== */
/* 9. avatar */
/* ======================================================================== */
.avatar {
  width: 100%;
  max-width: 82px;
  max-height: 82px;
  border-radius: 100%; }

@media only screen and (max-width: $sm) {
  .avatar {
    max-width: 60px;
    max-height: 60px; } }

/* ======================================================================== */
/* 10. backgrounds */
/* ======================================================================== */
.bg-light {
  background-color: $light; }
.bg-dark {
  background-color: $dark; }
.bg-black {
  background-color: $black; }
.bg-black-2 {
  background-color: $black2; }
.bg-white {
  background-color: $white; }
.bg-accent {
  background-color: $accent; }
.bg-fixed {
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center; }

/* ======================================================================== */
/* 11. burger */
/* ======================================================================== */
.burger {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 38px;
  height: 38px;
  cursor: pointer;
  padding: 0 6px;
  z-index: 600; }
.burger__line {
  width: 100%;
  height: 2px;
  margin: 2px 0;
  background-color: $accent;
  @include trans1; }
.burger_opened, .burger_opened:hover {
  .burger__line {
    &:nth-of-type(1) {
      transform: scaleX(1) rotate(45deg) translate(7px, 0px); }
    &:nth-of-type(2) {
      transform: scaleX(0); }
    &:nth-of-type(3) {
      transform: scaleX(1) rotate(-45deg) translate(8px, -1px); } } }

/* ======================================================================== */
/* 12. button */
/* ======================================================================== */
.button {
  position: relative;
  display: inline-block;
  padding: 16px 40px;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 25px;
  line-height: 1;
  letter-spacing: 0.5px;
  text-align: center;
  cursor: pointer;
  color: $accent;
  border-width: 2px;
  border-style: solid;
  border-color: transparent;
  background-color: transparent;
  @include trans1;
  &:focus {
    outline: none; } }
.button_arrow {
  padding: 0;
  &.button_white {
    color: $white;
    .button__text_line-right {
      &:after {
        background-color: $white;
        @include trans1; } } }
  &:hover {
    .button__text_line-right {
      &:after {
        transform: translateX(5px); } }
    .button__icon {
      transform: translateX(10px); } } }
.button__icon {
  @include trans1;
  display: inline-block; }
.button_bordered {
  &.button_accent {
    border-color: $accent;
    color: $accent;
    &:focus {
      color: $accent; }
    &:hover {
      color: $white;
      background-color: $accent; } }
  &.button_dark {
    border-color: $dark;
    color: $dark;
    &:focus {
      color: $dark; }
    &:hover {
      color: $white; } }
  &.button_light {
    border-color: $light;
    color: $light;
    &:focus {
      color: $light; }
    &:hover {
      color: $dark; } }
  &.button_black {
    border-color: #000;
    color: #000;
    &:focus {
      color: #000; }
    &:hover {
      color: $white; } }
  &.button_white {
    border-color: $white;
    color: $white;
    &:focus {
      color: $white; }
    &:hover {
      color: $dark;
      background-color: $white; } } }
.button_solid {
  &.button_accent {
    background-color: $accent;
    color: $white;
    &:focus {
      color: $white; }
    &:hover {
      border-color: $dark;
      background-color: $dark; }
    &.button_shadow {
      box-shadow: 0px 0px 30px 0px rgba(var(--color-accent-primary-rgb), 0.5);
      &:hover {
        box-shadow: 0px 0px 30px 0px rgba(24, 24, 24, 0.5); } } }
  &.button_dark {
    background-color: $dark;
    color: $white;
    &:focus {
      color: $white; }
    &:hover {
      border-color: $dark; }
    &.button_shadow {
      box-shadow: 0px 0px 30px 0px rgba(26, 26, 26, 0.2); } }
  &.button_light {
    background-color: $light;
    color: $dark;
    &:focus {
      color: $dark; }
    &:hover {
      border-color: $light; }
    &.button_shadow {
      box-shadow: 0px 0px 30px 0px rgba(245, 245, 245, 0.2); } }
  &.button_black {
    background-color: $black;
    color: $white;
    &:focus {
      color: $white; }
    &:hover {
      border-color: $dark; }
    &.button_shadow {
      box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2); } }
  &.button_white {
    background-color: $white;
    color: $accent;
    &:focus {
      color: $accent; }
    &:hover {
      border-color: $dark;
      background-color: $dark;
      color: $white; }
    &.button_shadow {
      box-shadow: 0px 0px 30px 0px rgba(255, 255, 255, 0.2); } } }
.button_fullwidth {
  width: 100%; }
.button_circle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 23px;
  width: 52px;
  height: 52px;
  border-radius: 100%;
  padding: 0; }
.button__text {}
.button__text_line {}
.button__text_line-right {
  &:after {
    content: '';
    display: inline-block;
    width: 43px;
    height: 1px;
    vertical-align: middle;
    margin-left: 12px;
    margin-right: -14px; } }

@media only screen and (max-width: $xl) {
  .button:not(.button_arrow, .button_circle) {
    padding: 14px 35px;
    font-size: 12px; } }

@media only screen and (max-width: $md) {
  .button__icon {
    max-height: 30px; } }


/* ======================================================================== */
/* 13. comments */
/* ======================================================================== */
.comments-area, .comment-respond {}
.comment-list {
  @include reset-ul;
  li {
    padding-top: 30px;
    padding-bottom: 30px; }
  > li:not(:last-child) {
 }    // border-bottom: 1px solid $borders
  ol {
    @include reset-ul;
    padding-left: 4%;
    li:last-child {
      padding-bottom: 0; } } }
.comments-title, .comment-reply-title {
  font-family: $font-secondary;
  font-size: 30px;
  font-weight: bold;
  line-height: 1.3;
  margin-top: 0; }
.comments-title {
  margin-bottom: 1em; }
.comment-reply-title {
  margin-top: 80px;
  margin-bottom: 1.5em; }
.comment-meta {
  display: flex;
  justify-content: space-between;
