.tabs__item_active {
  color: inherit;
  .tabs__icon {
    color: $accent;
    @include trans1; } }
.tabs__icon {
  font-size: 50px;
  margin-bottom: 5px; }
.tabs__underline {
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  background-color: $white;
  width: 0;
  height: 1px; }

@media only screen and (max-width: $md) {
  .tabs__underline {
    display: none; } }

@media only screen and (max-width: $sm) {
  .tabs__item {
    margin-top: 15px;
    margin-bottom: 15px; }
  .tabs__header {
    margin-bottom: 30px; } }

